<template>
  <div class="app-container">
    <el-dialog
      :title="title"
      :visible.sync="centerDialogVisible"
      width="40%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      center
    >
      <span>{{ remark }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="updateApp"></el-button>
        <el-button size="small" @click="centerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="downloadDialogVisible"
      :show-close="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title=""
      width="30%"
    >
      <div style="text-align:center">
        <el-progress type="circle" :percentage="downloadPercent"></el-progress>
      </div>
    </el-dialog>
  </div>
</template>


<!--  -->

<script>
import {
  version,
  description,
  author,
  license,
  homepage
} from '#/package.json'

import {
  webFrame,
  ipcRenderer,
  shell
} from 'electron'

export default {
  name: 'App',
  data() {
    return {
      version,
      description,
      author,
      license,
      homepage,
      dialogUpdateNow: false,
      downloading: false,
      hasNewVersion: false,
      noNewVersion: false,
      downloadPercent: 0,
      showError: false,
      errorInfo: {},
      versionInfoList: [],
      downloadPercent: '',
      // centerDialogVisible: false,
      downloadDialogVisible: false,
      title: '3.3.1',
      remark: '',
      centerDialogVisible: true,
      progress:100,
      active: false
    }
  },
  mounted () {
    this.checkForUpdates();
  },
  created() {
    ipcRenderer.on('dom-ready', e => {
      this.setZoomLevel()
    })
  },
  methods: {
    setZoomLevel() {
      // 
      webFrame.setZoomFactor(100)
      webFrame.setZoomLevel(0)
      webFrame.setVisualZoomLevelLimits(1, 1)
    },
    updateApp () {
      this.centerDialogVisible = false
      this.downloadDialogVisible = true
      this.downloadUpdate()
    },
    downloadUpdate () {
      ipcRenderer.send('downloadUpdate')
      // "downloadProgress
      ipcRenderer.on('downloadProgress', (event, progressObj) => {
        console.log(progressObj)
        this.downloadPercent = Math.trunc(progressObj.percent) || 0
        // this.downloadPercent = progressObj.percent.toFixed(2) || 0
        console.log(Math.trunc(this.downloadPercent))
        console.log(Math.trunc(this.downloadPercent) === 100)
        if (Math.trunc(this.downloadPercent) === 100) {
          console.log('...')
          ipcRenderer.on('isUpdateNow', function () {
            ipcRenderer.send('isUpdateNow')
          })
        }
      })
    },
    checkForUpdates() {
      ipcRenderer.send('ABOUTWIN:checkForUpdates');

    },
    openURL() {
      // shell.openExternal(this.homepage)
      ipcRenderer.send('icpRender-newWin', this.homepage)
    },

  }
}
</script>

